import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import AppIndex from '@/components/home/AppIndex'
import Home from '../components/home/Home.vue'
import LibraryIndex from '../components/library/LibraryIndex.vue'
import DiseaseTable from '../components/index/disease/DiseaseTable.vue'
import SymptomTable from '../components/index/symptom/SymptomTable.vue'
import MediclineTable from '../components/index/medicine/MediclineTable.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path:'/',
      redirect:'/login',
    },
    {
      path:'/login',
      name:'login',
      component:Login,
    },
    {
      path:'/home',
      name:'home',
      component:Home,
      redirect:'/index',
      children:[
        {
          path:'/index',
          name:'AppIndex',
          component:AppIndex,
          redirect:'/disease',
          meta: {
            requireAuth: true
          },
          children:[
            {
              path:'/disease',
              name:'DiseaseTable',
              component:DiseaseTable,
              meta: {
                requireAuth: true
              }
            },
            {
              path:'/symptom',
              name:'SymptomTable',
              component:SymptomTable,
              meta: {
                requireAuth: true
                }
            },
            {
              path:'/medicine',
              name:'MediclineTable',
              component:MediclineTable,
              meta: {
                requireAuth: true
                }
            },
            
          ],
        },
        {
          path:'/library',
          name:'library',
          component:LibraryIndex,
          meta:{
            requireAuth: true
          }
        }
      ]
    }
  ]
})



