<template>
  <div>
    <div>
      <SearchBar @onSearch="searchResult()" ref="searchBar" :showtext="showtext"></SearchBar>
      <AddSymptmo @onSubmit="loadSymptmos()" ref="add"></AddSymptmo>
    </div>
    <el-table
      :data="tableData"
      style="width: 100%"
      height="250">
      <el-table-column
        prop="disease"
        label="疾病名称"
        width="120"
        align="center">
      </el-table-column>
      <el-table-column
        fixed
        prop="disnumber"
        label="疾病编号"
        width="150"
        align="center">
      </el-table-column>
      <el-table-column
        prop="symptom"
        label="疾病症状"
        width="120"
        align="center">
      </el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import SearchBar from '../../library/SearchBar.vue'
  import AddSymptmo from './AddSymptmo.vue'
  export default{
    name:'SymptomTable',
    components:{
      SearchBar,
      AddSymptmo
    },
    data(){
      return{
        showtext:"输入疾病名查询",
        tableData:[{
          id:"",
          disease:"",
          disnumber:"",
          symptom:""
        }]
      }
    },
    mounted: function () {
      this.loadSymptmos()
    },
    methods:{
      loadSymptmos(){
        var _this = this
        this.$axios.get('/symptom').then(resp => {
          if (resp && resp.status === 200) {
            _this.tableData = resp.data
          }
        })
      },
      searchResult(){
        var _this = this
        this.$axios
          .get('/searchSymptom?keywords=' + this.$refs.searchBar.keywords, {
          }).then(resp => {
          if (resp && resp.status === 200) {
            _this.tableData = resp.data
          }
        })
      },
      handleEdit(index, row) {
        this.$refs.add.selctdisabled = true
        this.$refs.add.dialogFormVisible = true
        this.$refs.add.form = {
          id: row.id,
          disease:row.disease,
          disnumber:row.disnumber,
          symptom:row.symptom
        }
      },
      handleDelete(index, row) {
        this.$confirm('此操作将永久删除该项数据, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            this.$axios
              .post('/delSymptom', {id: row.id}).then(resp => {
              if (resp && resp.status === 200) {
                this.loadSymptmos()
              }
            })
          }
        ).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      },
    }
  }
</script>

<style>
</style>



