<template>
  <div>
    <el-button type="primary" @click="dialogFormVisible = true" >增加</el-button>
    <el-dialog
      title="添加/修改疾病"
      :visible.sync="dialogFormVisible"
      @close="clear">
      <el-form v-model="form" style="text-align: left" ref="dataForm">
        <el-form-item label="疾病名" :label-width="formLabelWidth" prop="disease">
          <el-select @change="selectMedicine($event)" v-model="form.disease" placeholder="请选择分类">
            <el-option v-for="(item,index) in prestrainform"
                       :key="index"
                       :label="item.disease"
                       :value="item.disease">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="疾病编号" :label-width="formLabelWidth" prop="disnumber">
          <el-select v-model="form.disnumber" placeholder="请选择分类">
            <el-option v-for="item in prestrainform"
                       :key="item.id"
                       :label="item.disnumber"
                       :value="item.disnumber">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="药品名称" :label-width="formLabelWidth" prop="medicine">
          <el-input v-model="form.medicine" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="疾病部门" :label-width="formLabelWidth" prop="department">
          <el-input v-model="form.department" autocomplete="off" ></el-input>
        </el-form-item>
        <el-form-item label="封面" :label-width="formLabelWidth" prop="cover">
          <el-input v-model="form.cover" autocomplete="off" placeholder="图片 URL"></el-input>
          <img-upload @onUpload="uploadImg" ref="imgUpload"></img-upload>
        </el-form-item>
        <el-form-item label="药品价格" :label-width="formLabelWidth" prop="price">
          <el-input v-model="form.price" autocomplete="off" ></el-input>
        </el-form-item>
        <el-form-item label="药品简介" :label-width="formLabelWidth" prop="abs">
          <el-input v-model="form.abs" autocomplete="off" ></el-input>
        </el-form-item>
        <el-form-item prop="id" style="height: 0">
          <el-input type="hidden" v-model="form.id" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="onSubmit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import ImgUpload from '../../library/ImgUpload.vue'
  export default{
    name:'AddMedicline',
    components:{
      ImgUpload
    },
    data(){
      return{
        dialogFormVisible: false,
        formLabelWidth: '120px',
        form:{

        },
        prestrainform:[

        ]
      }
    },
    mounted: function () {
      this.prestrain()
    },
    methods:{
      selectMedicine(e){
        if(e != null && e != '' && e != undefined){
          var _this = this
          this.$axios
            .get('/searchDisease?keywords=' +e, {
            }).then(resp => {
            if (resp && resp.status === 200) {
              // _this.tableData = resp.data
              // console.log(resp.data[0].disnumber);
              _this.form={
                disease: resp.data[0].disease,
                disnumber: resp.data[0].disnumber,
                department: resp.data[0].department
              }
            }
          })
        }
      },
      prestrain(){
        var _this = this
        this.$axios.get('/disease').then(resp => {
          if (resp && resp.status === 200) {
            _this.prestrainform = resp.data
          }
        })
      },
      clear(){
        this.form={
          id:"",
          disease:"",
          disnumber:"",
          medicine:"",
          department:"",
          cover:"",
          price:"",
          abs:""
        }
      },
      onSubmit () {
        this.$axios
          .post('/medicine', {
            id: this.form.id,
            disease:this.form.disease,
            disnumber:this.form.disnumber,
            medicine:this.form.medicine,
            department:this.form.department,
            cover:this.form.cover,
            price:this.form.price,
            abs:this.form.abs,
          }).then(resp => {
          if (resp && resp.status === 200) {
            this.dialogFormVisible = false
            this.$emit('onSubmit')
          }
        })
     },
      uploadImg () {
        this.form.cover = this.$refs.imgUpload.url
      }
    }
  }
</script>

<style>
</style>



