<template>
  <div>
    <el-button type="primary" @click="dialogFormVisible = true" >增加</el-button>
    <el-dialog
      title="添加/修改疾病"
      :visible.sync="dialogFormVisible"
      @close="clear">
      <el-form v-model="form" style="text-align: left" ref="dataForm">
        <el-form-item label="疾病名" :label-width="formLabelWidth" prop="disease">
          <el-select @change="selectDisease($event)" v-model="form.disease" placeholder="请选择分类" :disabled="selctdisabled">
            <el-option v-for="(item,index) in prestrainform"
                       :key="index"
                       :label="item.disease"
                       :value="item.disease">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="疾病编号" :label-width="formLabelWidth" prop="disnumber">
          <el-select v-model="form.disnumber" placeholder="请选择分类" :disabled="selctdisabled">
            <el-option v-for="item in prestrainform"
                       :key="item.id"
                       :label="item.disnumber"
                       :value="item.disnumber">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="疾病症状" :label-width="formLabelWidth" prop="symptom">
          <el-input v-model="form.symptom" autocomplete="off" ></el-input>
        </el-form-item>
        <el-form-item prop="id" style="height: 0">
          <el-input type="hidden" v-model="form.id" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="onSubmit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default{
    name:"AddSymptmo",
    data(){
      return{
        selctdisabled	:false,
        dialogFormVisible: false,
        formLabelWidth: '120px',
        form:{

        },
        prestrainform:[

        ]
      }
    },
    mounted: function () {
      this.prestrain()
    },
    methods:{
      selectDisease(e){
        if(e != null && e != '' && e != undefined){
          var _this = this
          this.$axios
            .get('/searchDisease?keywords=' +e, {
            }).then(resp => {
            if (resp && resp.status === 200) {
              // _this.tableData = resp.data
              // console.log(resp.data[0].disnumber);
              _this.form={
                disease: resp.data[0].disease,
                disnumber: resp.data[0].disnumber
              }
            }
          })
        }
      },
      prestrain(){
        var _this = this
        this.$axios.get('/disease').then(resp => {
          if (resp && resp.status === 200) {
            _this.prestrainform = resp.data
          }
        })
      },
      clear(){
        this.selctdisabled = false,
        this.form={
          id:"",
          disease:"",
          disnumber:"",
          symptom:""
        }
      },
      onSubmit () {
        this.$axios
          .post('/symptom', {
            id: this.form.id,
            disease:this.form.disease,
            disnumber:this.form.disnumber,
            symptom:this.form.symptom
          }).then(resp => {
          if (resp && resp.status === 200) {
            this.dialogFormVisible = false
            this.$emit('onSubmit')
          }
        })
      }
    },
 }
</script>

<style>
</style>



