<template>
  <div>
    <el-button type="primary" @click="dialogFormVisible = true" >增加</el-button>
    <el-dialog
      title="添加/修改疾病"
      :visible.sync="dialogFormVisible"
      @close="clear">
      <el-form v-model="form" style="text-align: left" ref="dataForm">
        <el-form-item label="疾病名" :label-width="formLabelWidth" prop="disease">
          <el-input v-model="form.disease" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="疾病编号" :label-width="formLabelWidth" prop="disnumber">
          <el-input v-model="form.disnumber" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="疾病分类" :label-width="formLabelWidth" prop="department">
          <el-select v-model="form.department" placeholder="请选择分类">
            <el-option label="普通门诊" value="普通门诊"></el-option>
            <el-option label="心血管科" value="心血管科"></el-option>
            <el-option label="呼吸科" value="呼吸科"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item prop="id" style="height: 0">
          <el-input type="hidden" v-model="form.id" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="onSubmit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default{
    name:'AddDisease',
    data () {
      return{
        dialogFormVisible: false,
        form: {
          id:"",
          disease:"",
          disnumber:"",
          department:""
        },
        formLabelWidth: '120px'
      }
    },
    methods:{
      clear(){
        this.form={
          id:"",
          disease:"",
          disnumber:"",
          department:""
        }
      },
      onSubmit () {
        this.$axios
          .post('/disease', {
            id: this.form.id,
            disease:this.form.disease,
            disnumber:this.form.disnumber,
            department:this.form.department
          }).then(resp => {
          if (resp && resp.status === 200) {
            this.dialogFormVisible = false
            this.$emit('onSubmit')
          }
        })
      }
    }
  }
</script>

<style>
</style>



