<template>
  <div>
    <el-radio-group v-model="isCollapse" style="margin-bottom: 20px;">
      <el-radio-button :label="true">收起</el-radio-button>
      <el-radio-button :label="false">展开</el-radio-button>
    </el-radio-group>
    <el-menu default-active="1-4-1"
             router
             class="el-menu-vertical-demo"
             @open="handleOpen"
             @close="handleClose"
             :collapse="isCollapse">
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span slot="title">业务管理</span>
        </template>
        <el-menu-item-group>
          <el-menu-item v-for="(item,i) in indexList" :key="i" :index="item.name">
            {{ item.indexItem }}
          </el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-menu-item index="/user">
        <i class="el-icon-location"></i>
        <span slot="title">用户管理</span>
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script>
  export default{
    name:'IndexMenu',
    data(){
      return{
        isCollapse: true,
        indexList:[
          {name: '/disease', indexItem: '疾病管理'},
          {name:'/symptom',indexItem:"症状管理"},
          {name: '/medicine', indexItem: '药品管理'},    
          {name: '/dector', indexItem: '医生管理'},
          {name: '/hospital', indexItem: '医院管理'}
        ]
      }
    },
    methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      }
    }
  }
</script>

<style>
  .el-menu-vertical-demo:not(.el-menu--collapse) {
      width: 200px;
      min-height: 400px;
    }
</style>



