<template>
  <body id="poster">
    <el-form class="login-container" label-position="left" label-width="0px" ref="formRef" :model="loginForm" :rules="rules">
      <h3 class="login_title">系统登录</h3>
      <el-form-item prop="username">
        <el-input auto-complete="off" placeholder="账号" type="text" v-model="loginForm.username"></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input auto-complete="off" placeholder="密码" type="password"  v-model="loginForm.password"></el-input>
      </el-form-item>
      <el-form-item class="btns">
        <el-button type="primary" @click="login">登录</el-button>
        <el-button type="info" @click="reset">重置</el-button>
      </el-form-item>
    </el-form>
  </body>
</template>

<script>
 export default{
   name:'Layout',
   data() {
         return {
           loginForm: {
             username: '',
             password: '',
           },

          rules: {
            username: [
              { required: true, message: "请输入用户名", trigger: "blur" },
              { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
            ],
            password: [
              { required: true, message: "请输入密码", trigger: "blur" },
              { min: 3, max: 6, message: '长度在 3 到 6 个字符', trigger: 'blur' }
            ],
         },
         responseResult: []
      };
   },

   methods: {
     reset(){
       this.$refs.formRef.resetFields();
     },
     // 1.点击登录按钮，向后端发送数据
     // 2.受到后端返回的成功代码时,触发store中的login()方法把loginForm对象传递给store中的user对象
     // （*这里只是简单的实现，在后端我们可以通过用户名和密码查询数据库,获得user表的完整信息，
     // 比如用户昵称、用户级别等，返回前端，并传递给 user 对象，以实现更复杂的功能）
     // 3.获取登录前页面的路径并跳转，如果该路径不存在，则跳转到首页
     login () {
       var _this = this
       console.log(this.$store.state)
       this.$axios
         .post('/login', {
           username: this.loginForm.username,
           password: this.loginForm.password
         })
         .then(successResponse => {
           if (successResponse.data.code === 200) {
             // var data = this.loginForm
             _this.$store.commit('login', _this.loginForm)
             var path = this.$route.query.redirect
             this.$router.replace({path: path === '/' || path === undefined ? '/index' : path})
           }
         })
         .catch(failResponse => {
         })
     }

   },
 };
</script>

<style>
  #poster {
      background:url("../assets/yasuo.jpg") no-repeat;
      background-position: center;
      height: 100%;
      width: 100%;
      background-size: cover;
      position: fixed;
    }
    body{
      margin: 0px;
    }
  .login-container {
      border-radius: 15px;
      background-clip: padding-box;
      margin: 90px auto;
      width: 350px;
      padding: 35px 35px 15px 35px;
      background: #fff;
      border: 1px solid #eaeaea;
      box-shadow: 0 0 25px #cac6c6;
    }

  .login_box {
    width: 450px;
    height: 300px;
    background-color: #fff;
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  .btns {
    display: flex;
    justify-content: flex-end;
  }

  .login_title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }

</style>



