<template>
  <div>
    <div>
      <SearchBar @onSearch="searchResult()" ref="searchBar" :showtext="showtext"></SearchBar>
      <AddMedicline @onSubmit="loadMedicine()" ref="add" ></AddMedicline>
    </div>
    <div>
      <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <div style="width: 50%;float:left;">
                <el-form-item label="疾病编号">
                  <span>{{ props.row.disnumber }}</span>
                </el-form-item>
                <el-form-item label="疾病名称">
                  <span>{{ props.row.disease }}</span>
                </el-form-item>
                <el-form-item label="药品名称">
                  <span>{{ props.row.medicine }}</span>
                </el-form-item>
                <el-form-item label="疾病部门">
                  <span>{{ props.row.department }}</span>
                </el-form-item>
                <el-form-item label="药品价格">
                  <span>{{ props.row.price }}</span>
                </el-form-item>
                <el-form-item label="药品描述">
                  <span>{{ props.row.abs }}</span>
                </el-form-item>
              </div>
              <div style="width: 50%;float:left;">
                <el-form-item label="商品图片">
                  <img class="cover" :src=" props.row.cover" alt="封面">
                  <!-- <span>{{ props.row.desc }}</span> -->
                </el-form-item>
              </div>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          label="疾病名称"
          prop="disease">
        </el-table-column>
        <el-table-column
          label="药品名称"
          prop="medicine">
        </el-table-column>
        <el-table-column
          label="药品描述"
          prop="abs">
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
  import AddMedicline from './AddMedicline.vue'
  import SearchBar from '../../library/SearchBar.vue'
  export default{
    name:"MediclineTable",
    components:{
      AddMedicline,
      SearchBar
    },
    data(){
      return {
        showtext:"输入药品名查询",
        tableData:[{

        }]
      }
    },
    mounted: function () {
      this.loadMedicine()
    },
    methods:{
      loadMedicine(){
        var _this = this
        this.$axios.get('/medicine').then(resp => {
          if (resp && resp.status === 200) {
            _this.tableData = resp.data
          }
        })
      },
      handleEdit(index, row) {
        console.log(index, row);
        this.$refs.add.dialogFormVisible = true
        this.$refs.add.form = {
          id: row.id,
          disease:row.disease,
          disnumber:row.disnumber,
          medicine:row.medicine,
          department:row.department,
          cover:row.cover,
          price:row.price,
          abs:row.abs,
        }
      },
      handleDelete(index, row) {
        console.log(index, row,row.id);
        this.$confirm('此操作将永久删除该项数据, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            this.$axios
              .post('/delmedicine', {id: row.id}).then(resp => {
              if (resp && resp.status === 200) {
                this.loadMedicine()
              }
            })
          }
        ).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      },
      searchResult(){
        var _this = this
        this.$axios
          .get('/searchMedicine?keywords=' + this.$refs.searchBar.keywords, {
          }).then(resp => {
          if (resp && resp.status === 200) {
            _this.tableData = resp.data
          }
        })
      }

    }
  }
</script>

<style>
  .demo-table-expand {
      font-size: 0;
    }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
  .cover {
    width: 115px;
    height: 172px;
    margin-bottom: 7px;
    overflow: hidden;
    cursor: pointer;
  }
</style>



